.timeline {
	width: 100%;
}

.timeline .timeline-item {
	position: relative;
	width: 50%;
	margin-left: 50%;
	padding-bottom: 1.5rem;
	padding-left: 1.5rem;
	text-align: left;
}

@media (max-width: 575.98px) {
	.timeline .timeline-item {
		width: 100%;
		margin-left: 0;
	}
}
.timeline .timeline-item:before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 16px;
	height: 16px;
	margin-top: 0.425rem;
	margin-left: -0.5rem;
	border: 0.0625rem solid #dce6f3;
	border-radius: 50%;
}

.timeline .live-investment::before {
	background-color: red;
	border-color: white;
	animation: pulse-animation 2s infinite;
}

@keyframes pulse-animation {
	0% {
		box-shadow: 0 0 0 0px rgba(255, 0, 0, 0.2);
	}
	100% {
		box-shadow: 0 0 0 8px rgba(255, 0, 0, 0);
	}
}
.timeline .timeline-item:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: -0.1875rem;
	width: 0.0625rem;
	margin-top: 1.875rem;
	margin-left: -0.0625rem;
	background-color: #dce6f3;
}

.timeline .timeline-item:nth-child(2n+1) {
	margin-left: 0;
	padding-right: 1.5rem;
	padding-left: 0;
	text-align: right;
}

@media (max-width: 575.98px) {
	.timeline .timeline-item:nth-child(2n+1) {
		padding-right: 0;
		padding-left: 1.5rem;
		text-align: left;
	}
}
.timeline .timeline-item:nth-child(2n+1):after,
.timeline .timeline-item:nth-child(2n+1):before {
	right: 0;
	left: auto;
}

@media (max-width: 575.98px) {
	.timeline .timeline-item:nth-child(2n+1):after,
.timeline .timeline-item:nth-child(2n+1):before {
		right: auto;
		left: 0;
	}
}
.timeline .timeline-item:nth-child(2n+1):before {
	margin-right: -0.5rem;
}

.timeline .timeline-item:nth-child(2n+1):after {
	margin-right: -0.0625rem;
}

.timeline.dark .timeline-item:before {
	border: 0.0625rem solid #b7c3d2;
}

.timeline.dark .timeline-item:after {
	background-color: #b7c3d2;
}

.tooltip-referral {
	position: relative;
	display: block;
}

.tooltip-referral .tooltiptext {
	visibility: hidden;
	text-align: center;
	border-radius: 6px;
	padding: 5px;
	position: absolute;
	z-index: 1;
	bottom: 150%;
	left: 50%;
	opacity: 0;
	transition: opacity 0.3s;
	transform: translate(-50%, 50%);
}

.tooltip-referral:hover .tooltiptext {
	visibility: visible;
	opacity: 1;
}

